import React, { Component } from 'react';
import './style.css';
import ServicesList from './components/ServicesList';
import Contact from './components/Contact';

class App extends Component {
  constructor() {
    super();
    this.state = {
      mobileMenuActive: false
    }
  }

  toggleMobileNav = () => {
    this.setState({
      mobileMenuActive: !this.state.mobileMenuActive
    });
  }

  scrollTo = (el) => {
    window.scroll({
      top: document.querySelector(el).getBoundingClientRect().top + window.scrollY,
      left: 0,
      behavior: 'smooth'
    });
  }

  render() {
    var activeClass = "";
    var isActive = "";
    if(this.state.mobileMenuActive) {
      activeClass = 'mobile';
      isActive = 'is-active';
    }
    
    return (
      <main className={"main_ " + activeClass}>
        <div className="pre-loader"></div>
        <div className="hero-banner">
          <div className="hero-overlay"></div>
          <nav className="navbar-wrap">
            <div className="cwidth navbar">
              <a className="navbar-logo" href="#"><img src="/imgs/logo.svg"/></a>
              <div className="nav-items">
                <a href="tel:905-554-2880" className="nav-phone"><i className="material-icons">phone</i> (905)&bull;554&bull;2880</a>
              </div>
            </div>
          </nav>
          <div className="hero-content">
            <div className="hero-text">The most elegant nail salon in Markham</div><br/>
            <a href="tel:905-554-2880" className="hero-button">Call (905)&bull;554&bull;2880</a>
          </div>
        </div>
        <div className="content-section about cwidth">
          <div className="section-header">About</div>
          At The Nail Chamber we understand that everyone has different needs. That is why we offer a unique service for each of our clients to ensure they receive individual treatments that are best suited to them. Our team is constantly seeking out new products and services for our clients' enjoyment and satisfaction.
          <br/><br/>
          We pride ourselves on providing our customers
          with fabulous nail care while maintaining the
          highest level of cleanliness and sterilization.
          All of our equipment and tools are UV sterilized.
          We also use disposable spa liners, files and buffers.
        </div>
        <ServicesList />
        <Contact />
        
        <footer className="footer">
          <div class="social-icons">
              <a href=""></a>
          </div>
        </footer>
        <nav className="mobile-nav">
          <ul className="navmenu">
            <li>Home</li>
            <li>Services</li>
            <li>Contact</li>
          </ul>
        </nav>
      </main>
    );
  }
}

export default App;
